import React from "react";

const LoaderContainer = ({
  backgroundColor = "#0000003a",
  opacity = 1,
}: {
  backgroundColor: string;
  opacity: number;
}) => {
  return (
    <div
      id="rgl-overlay"
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        zIndex: 1000,
        opacity: opacity,
        background: backgroundColor,
        transition: "opacity 0.2s ease",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <div>Loading</div>
    </div>
  );
};

export default LoaderContainer;
